import styled from "styled-components";
import DiaryItem from "./DiaryItem";
import { useDiaryDispatchContext, useDiaryValueContext } from "../context/DiaryProvider";
import {useState,useEffect} from "react";

interface SortData{
    isSort:string
}

function DiaryList({isSort}:SortData) {
    //localstorage 
    //const data = JSON.parse(`${localStorage.getItem("diary")}`)==null? [] : JSON.parse(`${localStorage.getItem("diary")}`);
    const data =useDiaryValueContext();
    const dispatch=useDiaryDispatchContext();

    //sort   
    //localStorage setItem         
    //sortData    localstorage   dispatch localstorage setItem 
    //create newState=[action.diaryItems,...state];          x
    const [sortData,setSortData]=useState([...data]);

    useEffect(()=>{
        if(isSort==="write"){
            data.sort((a,b)=>b.idx-a.idx)
            setSortData([...data].sort((a,b)=>b.idx-a.idx))
        }
        else if(isSort==="date"){
            data.sort((a,b)=>parseInt(a.date.toString().replace(/-/g,""))-parseInt(b.date.toString().replace(/-/g,"")))
            setSortData([...data].sort((a,b)=>parseInt(a.date.toString().replace(/-/g,""))-parseInt(b.date.toString().replace(/-/g,""))))
        }
        else if(isSort==="thema"){
            //(a,b)=>(a.thema).localeCompare(b.thema) or a.thema>b.thema?1:-1 (  )
            data.sort((a,b)=>(a.thema).localeCompare(b.thema))
            setSortData([...data].sort((a,b)=>(a.thema).localeCompare(b.thema)))
        }
        dispatch({type:'SORT',data})
    },[isSort])

    return (
        <Box className="box">
            {sortData.map((item, index:number) => {
                return <DiaryItem key={index} id={index} thema={item.thema} img={item.img} date={item.date} />
            })}
        </Box>
    );
}

export default DiaryList;

const Box = styled.div`
  display:flex;
  flex-flow:row wrap;
  width:650px;
  height:420px;
  overflow:auto;
  margin-left:15px;
`;